@charset "UTF-8";
/* Указываем box sizing */
*,
*::before,
*::after {
  box-sizing: border-box;
}

/* Убираем внутренние отступы */
ul[class],
ol[class] {
  padding: 0;
}

/* Убираем внешние отступы */
body,
h1,
h2,
h3,
h4,
p,
ul[class],
ol[class],
li,
figure,
figcaption,
blockquote,
dl,
dd {
  margin: 0;
}

/* Выставляем основные настройки по-умолчанию для body */
body {
  min-height: 100vh;
  scroll-behavior: smooth;
  text-rendering: optimizeSpeed;
  line-height: 1.5;
}

/* Удаляем стандартную стилизацию для всех ul и il, у которых есть атрибут class*/
ul[class],
ol[class] {
  list-style: none;
}

/* Элементы a, у которых нет класса, сбрасываем до дефолтных стилей */
a:not([class]) {
  text-decoration-skip-ink: auto;
}

a {
  text-decoration: none;
}

/* Упрощаем работу с изображениями */
img {
  max-width: 100%;
  display: block;
  height: auto;
}

/* Указываем понятную периодичность в потоке данных у article*/
article > * + * {
  margin-top: 1em;
}

/* Наследуем шрифты для инпутов и кнопок */
input,
button,
textarea,
select {
  font: inherit;
}

/* Удаляем все анимации и переходы для людей, которые предпочитают их не использовать */
@media (prefers-reduced-motion: reduce) {
  * {
    animation-duration: 0.01ms !important;
    animation-iteration-count: 1 !important;
    transition-duration: 0.01ms !important;
    scroll-behavior: auto !important;
  }
}

body {
  font-family: 'Montserrat', sans-serif;
  font-size: 16px;
  color: #fff;
}

.container {
  max-width: 1300px;
  margin: 0px auto;
  padding: 0 15px;
}

.header {
  margin-bottom: 100px;
}

@media (max-width: 495px) {
  .header {
    margin-bottom: 40px;
  }
}

.header__logo {
  display: block;
  background: #fff;
  border-radius: 15px;
  padding: 12px 15px;
  margin-top: -12px;
}

@media (max-width: 1250px) {
  .header__logo {
    display: inline-block;
    margin-bottom: 15px;
  }
}

@media (max-width: 1028px) {
  .header__logo {
    margin: -12px auto 20px auto;
  }
}

.header__btn {
  text-align: center;
  border-radius: 40px;
  border: 2px solid #fff;
  width: 223px;
  height: 45px;
  font-weight: 900;
  font-size: 15px;
  line-height: 18px;
  color: #fff;
  display: flex;
  align-items: center;
  justify-content: center;
  transition: all 0.2s;
  margin: 0 5px;
}

.header__btn:hover {
  background: rgba(238, 234, 234, 0.1);
}

@media (max-width: 1028px) {
  .header__btn {
    margin-bottom: 10px;
  }
}

.header-content {
  display: flex;
  align-items: center;
  justify-content: space-between;
}

@media (max-width: 1250px) {
  .header-content {
    flex-wrap: wrap;
  }
}

@media (max-width: 1250px) {
  .header-btns {
    width: 70%;
  }
}

@media (max-width: 1028px) {
  .header-btns {
    width: 100%;
    margin-bottom: 20px;
  }
}

.main {
  background: url("../img/overlay.jpg") no-repeat center/cover;
  min-height: 100vh;
  padding-bottom: 50px;
}

.register {
  position: absolute;
  bottom: -30px;
  left: 10px;
  text-align: center;
  font-weight: 500;
  font-size: 15px;
}

.register a {
  text-decoration: underline;
  color: #fff;
}

@media (max-width: 495px) {
  .register {
    position: relative;
    bottom: auto;
    left: auto;
  }
}

.login-btn a {
  transition: all 0.2s;
  display: flex;
  align-items: center;
  justify-content: center;
  width: 219px;
  color: #000;
  font-weight: 700;
  font-size: 16px;
  height: 45px;
  background: #fff;
  border-radius: 40px;
}

.login-btn a:hover {
  background: #f1eded;
}

@media (max-width: 495px) {
  .login-btn a {
    margin: 0px auto 5px auto;
  }
}

.language-picker {
  display: flex;
  align-items: center;
}

@media (max-width: 1030px) {
  .language-picker {
    width: 8%;
  }
}

@media (max-width: 495px) {
  .language-picker {
    order: 4;
    margin: 10px auto 0 auto;
    width: 45px;
    height: 45px;
  }
}

.login {
  position: relative;
}

@media (max-width: 1250px) {
  .login {
    margin-left: auto;
  }
}

@media (max-width: 495px) {
  .login {
    width: 100%;
    text-align: center;
  }
}

.btns-list {
  display: flex;
  align-items: center;
  justify-content: space-between;
  flex-wrap: wrap;
}

@media (max-width: 728px) {
  .btns-list {
    justify-content: center;
  }
}

.main-content {
  max-width: 700px;
}

@media (max-width: 495px) {
  .main-content {
    text-align: center;
  }
}

.main-content__title {
  font-weight: 900;
  font-size: 40px;
  line-height: 49px;
  color: #fff;
  margin-bottom: 25px;
}

@media (max-width: 495px) {
  .main-content__title {
    line-height: 43px;
  }
}

@media (max-width: 365px) {
  .main-content__title {
    font-size: 36px;
  }
}

.main-content__text {
  color: #fff;
  margin-bottom: 25px;
}

.main-content__input {
  position: relative;
  max-width: 470px;
}

.main-content__search input {
  height: 50px;
  padding: 0 62px 0 10px;
  outline: none;
  width: 100%;
  border: none;
  border-top-left-radius: 4px;
  border-bottom-left-radius: 4px;
  border-top-right-radius: 8px;
  border-bottom-right-radius: 8px;
}

.main-content .choose-language-class {
  position: absolute;
  right: -3px;
  top: 0;
  bottom: 0;
  width: 50px;
  height: 50px;
  display: flex;
  align-items: center;
  justify-content: center;
  background: #a11d59;
  border-top-right-radius: 8px;
  border-bottom-right-radius: 8px;
}

.main-content .choose-language-class .search-icon {
  width: 25px;
  height: 25px;
}

.text {
  font-weight: 400;
  font-size: 16px;
  line-height: 25px;
}

.select-hidden {
  display: none;
  visibility: hidden;
  padding-right: 10px;
}

.select {
  cursor: pointer;
  display: inline-block;
  position: relative;
  font-weight: 900;
  font-size: 15px;
  color: #fff;
  width: 45px;
  height: 45px;
  border: 2px solid #fff;
  border-radius: 4px;
}

.select-styled {
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  display: flex;
  align-items: center;
  justify-content: center;
  transition: all 0.2s;
}

.select-styled:hover {
  background: rgba(238, 234, 234, 0.1);
}

.select-styled:active, .select-styled.active {
  background-color: rgba(238, 234, 234, 0.1);
}

.select-options {
  display: none;
  position: absolute;
  top: 100%;
  right: 0;
  left: 0;
  z-index: 999;
  margin: 0;
  padding: 0;
  list-style: none;
  background-color: #4b4a4a;
}

.select-options li {
  text-align: center;
  margin: 0;
  padding: 12px 0;
}

.select-options li:hover {
  background: rgba(238, 234, 234, 0.1);
}

.select-options li[rel='hide'] {
  display: none;
}
